import { useState, useEffect } from "preact/hooks";
import versionbanner from "../../assets/cdimages/cdbg.webp";
import mobilebanner from "../../assets/cdimages/mobile-bg.webp";
import cdstyle from "./style.css";
import CDStyleButton from "./styleButton";

const CDBannerVersion = () => {
  const initialScreen = window.innerWidth;
  const [screenSize, setScreenSize] = useState(initialScreen);

  useEffect(() => {
    setScreenSize(initialScreen);
  }, [initialScreen]);
  return (
    <div class={cdstyle.bannerVersionContent}>
      {screenSize >= 767 ? (
        <img src={versionbanner} alt="banner" />
      ) : (
        <img src={mobilebanner} alt="mobilebanner" />
      )}

      <div class={cdstyle.bannerContainer}>
        <div class={cdstyle.bannerversionContentContainer}>
          {/* <div class={cdstyle.bannerContentLeft}> */}
          <div
            class={`${cdstyle["bannerContentLeft"]} ${cdstyle["bannerContentversion"]}`}
          >
            <h2>Immigrate to Canada</h2>
            <p>Check your visa options with a representative today.</p>
            {/* <button class={cdstyle.cdbtn}>Check Your Eligibility</button> */}
            <CDStyleButton />
            {/* </div> */}
          </div>
        </div>
      </div>
    </div>
  );
};
export default CDBannerVersion;
