import cdstyle from "./style.css";

const CDStyleButton = ({ props }) => {
  let txt = "Check Your Eligibility";
  const getText=()=>{return txt.split("").map((t,i)=><span key={i}>{t}</span>)}
  const ScrollToEligibilitySection=()=>{let Why=document.getElementById('How')
  if(Why){Why.scrollIntoView()}else{window.location.href='#How'}}
  return (
    <div class={cdstyle.cdStyleContent}>
      <button {...props} class={cdstyle.cdButton} type='submit' onClick={ScrollToEligibilitySection}>
        <div>{getText()}</div>
      </button>
    </div>
  );
};
export default CDStyleButton;
